import { Component, OnInit } from '@angular/core';
import {Router} from '@angular/router';
import {OnoffserviceService } from '../../services/onoffservice.service'

@Component({
  selector: 'app-onoff',
  templateUrl: './onoff.component.html',
  styleUrls: ['./onoff.component.css']
})
export class OnoffComponent implements OnInit {


response:string;
value:string;


  constructor( private SERVICE: OnoffserviceService,private router :Router) { }

  ngOnInit() {
  }


goto(a){
  const body={
    value:a
  };
  this.SERVICE.LedOnOff(body).subscribe(res=>{
    console.log(res);
    if(res.success && a==='1'){
      this.response='LED ON';
    } else {
      this.response='LED OFF';
    }
  });
}

}
